import React, { useState } from 'react';
import {
  Box,
  Container,
  Typography,
  TextField,
  Button,
  Paper,
  Grid,
  Stepper,
  Step,
  StepLabel,
  StepContent,
  Card,
  CardContent,
  Chip,
  Alert,
  useTheme,
  useMediaQuery,
} from '@mui/material';
import {
  Search,
  LocalShipping,
  CheckCircle,
  Schedule,
  LocationOn,
  AccessTime,
} from '@mui/icons-material';
import { useSearchParams } from 'react-router-dom';

interface TrackingInfo {
  trackingNumber: string;
  status: string;
  sender: string;
  receiver: string;
  packageType: string;
  weight: string;
  createdAt: string;
  estimatedDelivery: string;
  steps: TrackingStep[];
}

interface TrackingStep {
  status: string;
  description: string;
  location: string;
  timestamp: string;
  completed: boolean;
}

const TrackingPage: React.FC = () => {
  const [searchParams] = useSearchParams();
  const [trackingNumber, setTrackingNumber] = useState(searchParams.get('number') || '');
  const [trackingResult, setTrackingResult] = useState<TrackingInfo | null>(null);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState('');
  const theme = useTheme();
  const isMobile = useMediaQuery(theme.breakpoints.down('md'));

  // 
  const mockTrackingData: { [key: string]: TrackingInfo } = {
    'ML123456789': {
      trackingNumber: 'ML123456789',
      status: '',
      sender: ' - ',
      receiver: ' - ',
      packageType: '',
      weight: '0.5kg',
      createdAt: '2024-01-14',
      estimatedDelivery: '2024-01-15',
      steps: [
        {
          status: '',
          description: '',
          location: '',
          timestamp: '2024-01-14 09:00',
          completed: true,
        },
        {
          status: '',
          description: '',
          location: '',
          timestamp: '2024-01-14 14:30',
          completed: true,
        },
        {
          status: '',
          description: '',
          location: '',
          timestamp: '2024-01-15 08:00',
          completed: false,
        },
        {
          status: '',
          description: '',
          location: '',
          timestamp: '',
          completed: false,
        },
        {
          status: '',
          description: '',
          location: '',
          timestamp: '',
          completed: false,
        },
      ],
    },
    'ML987654321': {
      trackingNumber: 'ML987654321',
      status: '',
      sender: ' - ',
      receiver: ' - ',
      packageType: '',
      weight: '2.0kg',
      createdAt: '2024-01-12',
      estimatedDelivery: '2024-01-14',
      steps: [
        {
          status: '',
          description: '',
          location: '',
          timestamp: '2024-01-12 10:00',
          completed: true,
        },
        {
          status: '',
          description: '',
          location: '',
          timestamp: '2024-01-12 15:00',
          completed: true,
        },
        {
          status: '',
          description: '',
          location: '',
          timestamp: '2024-01-13 09:00',
          completed: true,
        },
        {
          status: '',
          description: '',
          location: '',
          timestamp: '2024-01-14 10:00',
          completed: true,
        },
        {
          status: '',
          description: '',
          location: '',
          timestamp: '2024-01-14 14:30',
          completed: true,
        },
      ],
    },
  };

  const handleSearch = async () => {
    if (!trackingNumber.trim()) {
      setError('');
      return;
    }

    setLoading(true);
    setError('');
    setTrackingResult(null);

    // API
    setTimeout(() => {
      const result = mockTrackingData[trackingNumber];
      if (result) {
        setTrackingResult(result);
      } else {
        setError('');
      }
      setLoading(false);
    }, 1000);
  };

  const getStatusColor = (status: string) => {
    switch (status) {
      case '':
        return 'success';
      case '':
        return 'warning';
      case '':
        return 'info';
      case '':
        return 'primary';
      default:
        return 'default';
    }
  };

  const getStatusIcon = (status: string) => {
    switch (status) {
      case '':
        return <CheckCircle />;
      case '':
        return <LocalShipping />;
      case '':
        return <Schedule />;
      case '':
        return <LocationOn />;
      default:
        return <AccessTime />;
    }
  };

  return (
    <Box>
      {/* Hero Section */}
      <Box
        sx={{
          background: 'linear-gradient(135deg, #1976d2 0%, #42a5f5 100%)',
          color: 'white',
          py: { xs: 6, md: 8 },
          textAlign: 'center',
        }}
      >
        <Container maxWidth="lg">
          <Typography
            variant={isMobile ? 'h3' : 'h2'}
            component="h1"
            sx={{ fontWeight: 700, mb: 3 }}
          >
            
          </Typography>
          <Typography variant="h6" sx={{ opacity: 0.9, maxWidth: 600, mx: 'auto' }}>
            
          </Typography>
        </Container>
      </Box>

      <Container maxWidth="lg" sx={{ py: 6 }}>
        {/*  */}
        <Paper sx={{ p: 4, mb: 4 }}>
          <Typography variant="h5" component="h2" sx={{ mb: 3, fontWeight: 600, textAlign: 'center' }}>
            
          </Typography>
          
          <Grid container spacing={3} alignItems="center" justifyContent="center">
            <Grid item xs={12} md={6}>
              <TextField
                fullWidth
                placeholder=""
                value={trackingNumber}
                onChange={(e) => setTrackingNumber(e.target.value)}
                onKeyPress={(e) => e.key === 'Enter' && handleSearch()}
                variant="outlined"
                size="medium"
                sx={{ mb: { xs: 2, md: 0 } }}
              />
            </Grid>
            <Grid item xs={12} md={3}>
              <Button
                fullWidth
                variant="contained"
                onClick={handleSearch}
                disabled={loading}
                startIcon={loading ? null : <Search />}
                sx={{ py: 1.5, fontSize: '1.1rem' }}
              >
                {loading ? '...' : ''}
              </Button>
            </Grid>
          </Grid>

          {error && (
            <Alert severity="error" sx={{ mt: 3 }}>
              {error}
            </Alert>
          )}
        </Paper>

        {/*  */}
        {trackingResult && (
          <Grid container spacing={4}>
            {/*  */}
            <Grid item xs={12} md={4}>
              <Card>
                <CardContent>
                  <Typography variant="h6" component="h3" sx={{ mb: 3, fontWeight: 600 }}>
                    
                  </Typography>
                  
                  <Box sx={{ mb: 2 }}>
                    <Typography variant="body2" color="text.secondary">
                      
                    </Typography>
                    <Typography variant="body1" sx={{ fontWeight: 600, fontFamily: 'monospace' }}>
                      {trackingResult.trackingNumber}
                    </Typography>
                  </Box>

                  <Box sx={{ mb: 2 }}>
                    <Typography variant="body2" color="text.secondary">
                      
                    </Typography>
                    <Chip
                      label={trackingResult.status}
                      color={getStatusColor(trackingResult.status) as any}
                      icon={getStatusIcon(trackingResult.status)}
                      sx={{ mt: 1 }}
                    />
                  </Box>

                  <Box sx={{ mb: 2 }}>
                    <Typography variant="body2" color="text.secondary">
                      
                    </Typography>
                    <Typography variant="body1">
                      {trackingResult.sender}
                    </Typography>
                  </Box>

                  <Box sx={{ mb: 2 }}>
                    <Typography variant="body2" color="text.secondary">
                      
                    </Typography>
                    <Typography variant="body1">
                      {trackingResult.receiver}
                    </Typography>
                  </Box>

                  <Box sx={{ mb: 2 }}>
                    <Typography variant="body2" color="text.secondary">
                      
                    </Typography>
                    <Typography variant="body1">
                      {trackingResult.packageType}
                    </Typography>
                  </Box>

                  <Box sx={{ mb: 2 }}>
                    <Typography variant="body2" color="text.secondary">
                      
                    </Typography>
                    <Typography variant="body1">
                      {trackingResult.weight}
                    </Typography>
                  </Box>

                  <Box sx={{ mb: 2 }}>
                    <Typography variant="body2" color="text.secondary">
                      
                    </Typography>
                    <Typography variant="body1">
                      {trackingResult.createdAt}
                    </Typography>
                  </Box>

                  <Box>
                    <Typography variant="body2" color="text.secondary">
                      
                    </Typography>
                    <Typography variant="body1" sx={{ fontWeight: 600, color: 'primary.main' }}>
                      {trackingResult.estimatedDelivery}
                    </Typography>
                  </Box>
                </CardContent>
              </Card>
            </Grid>

            {/*  */}
            <Grid item xs={12} md={8}>
              <Card>
                <CardContent>
                  <Typography variant="h6" component="h3" sx={{ mb: 3, fontWeight: 600 }}>
                    
                  </Typography>
                  
                  <Stepper orientation="vertical">
                    {trackingResult.steps.map((step, index) => (
                      <Step key={index} active={step.completed} completed={step.completed}>
                        <StepLabel
                          icon={step.completed ? <CheckCircle color="success" /> : <Schedule />}
                          sx={{
                            '& .MuiStepLabel-label': {
                              color: step.completed ? 'text.primary' : 'text.secondary',
                              fontWeight: step.completed ? 600 : 400,
                            },
                          }}
                        >
                          <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
                            <Typography variant="body1" sx={{ fontWeight: 600 }}>
                              {step.status}
                            </Typography>
                            {step.timestamp && (
                              <Typography variant="body2" color="text.secondary">
                                {step.timestamp}
                              </Typography>
                            )}
                          </Box>
                        </StepLabel>
                        <StepContent>
                          <Box sx={{ mb: 2 }}>
                            <Typography variant="body2" color="text.secondary" sx={{ mb: 1 }}>
                              {step.description}
                            </Typography>
                            <Box sx={{ display: 'flex', alignItems: 'center', gap: 1 }}>
                              <LocationOn sx={{ fontSize: 16, color: 'text.secondary' }} />
                              <Typography variant="body2" color="text.secondary">
                                {step.location}
                              </Typography>
                            </Box>
                          </Box>
                        </StepContent>
                      </Step>
                    ))}
                  </Stepper>
                </CardContent>
              </Card>
            </Grid>
          </Grid>
        )}

        {/*  */}
        {!trackingResult && !loading && (
          <Paper sx={{ p: 4, textAlign: 'center' }}>
            <Typography variant="h6" component="h3" sx={{ mb: 3, fontWeight: 600 }}>
              
            </Typography>
            <Typography variant="body1" color="text.secondary" sx={{ mb: 2 }}>
              
            </Typography>
            <Box sx={{ display: 'flex', gap: 2, justifyContent: 'center', flexWrap: 'wrap' }}>
              {Object.keys(mockTrackingData).map((number) => (
                <Chip
                  key={number}
                  label={number}
                  variant="outlined"
                  onClick={() => {
                    setTrackingNumber(number);
                    handleSearch();
                  }}
                  sx={{ cursor: 'pointer', '&:hover': { backgroundColor: 'primary.50' } }}
                />
              ))}
            </Box>
          </Paper>
        )}
      </Container>
    </Box>
  );
};

export default TrackingPage;
