import React, { useEffect, useState } from 'react';
import {
  Box,
  Container,
  Paper,
  TextField,
  Button,
  Typography,
  Alert,
  CircularProgress,
  Card,
  CardContent,
  InputAdornment,
  IconButton,
  useTheme,
} from '@mui/material';
import {
  Visibility,
  VisibilityOff,
  Lock,
  Person,
  LocalShipping,
} from '@mui/icons-material';
import { useNavigate } from 'react-router-dom';

const AdminLogin: React.FC = () => {
  const [username, setUsername] = useState('');
  const [password, setPassword] = useState('');
  const [showPassword, setShowPassword] = useState(false);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState('');
  const navigate = useNavigate();
  const theme = useTheme();

  // 
  const defaultMaster = { username: 'master', password: 'marketlink#2025', role: 'master' } as const;

  // 
  useEffect(() => {
    try {
      const raw = localStorage.getItem('users');
      const users: Array<{ username: string; password: string; role: string }>|null = raw ? JSON.parse(raw) : null;
      if (!users || users.length === 0) {
        localStorage.setItem('users', JSON.stringify([defaultMaster]));
      } else {
        const hasMaster = users.some(u => u.username === defaultMaster.username);
        if (!hasMaster) {
          localStorage.setItem('users', JSON.stringify([defaultMaster, ...users]));
        }
      }
    } catch {}
  }, []);

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    
    if (!username.trim() || !password.trim()) {
      setError('');
      return;
    }

    setLoading(true);
    setError('');

    // API
    setTimeout(() => {
      try {
        const raw = localStorage.getItem('users');
        const users: Array<{ username: string; password: string; role: string }> = raw ? JSON.parse(raw) : [];
        const user = users.find(u => u.username === username && u.password === password);
        if (user) {
          localStorage.setItem('adminUser', JSON.stringify({
            username: user.username,
            role: user.role,
            loginTime: new Date().toISOString(),
          }));
          navigate('/admin/dashboard');
        } else {
          setError('');
        }
      } catch (e) {
        setError('');
      }
      setLoading(false);
    }, 600);
  };

  // 

  return (
    <Box
      sx={{
        minHeight: '100vh',
        background: 'linear-gradient(135deg, #667eea 0%, #764ba2 100%)',
        display: 'flex',
        alignItems: 'center',
        py: 4,
      }}
    >
      <Container maxWidth="sm">
        <Paper
          elevation={24}
          sx={{
            p: 4,
            borderRadius: 3,
            backgroundColor: 'rgba(255, 255, 255, 0.95)',
            backdropFilter: 'blur(10px)',
          }}
        >
          {/*  */}
          <Box sx={{ textAlign: 'center', mb: 4 }}>
            <Box sx={{ display: 'flex', justifyContent: 'center', mb: 2 }}>
              <LocalShipping sx={{ fontSize: 48, color: 'primary.main', mr: 2 }} />
            </Box>
            <Typography variant="h4" component="h1" sx={{ fontWeight: 700, mb: 1 }}>
              ML Express
            </Typography>
            <Typography variant="h6" component="h2" color="text.secondary">
              
            </Typography>
          </Box>

          {/*  */}
          <form onSubmit={handleSubmit}>
            <TextField
              fullWidth
              label=""
              value={username}
              onChange={(e) => setUsername(e.target.value)}
              variant="outlined"
              margin="normal"
              required
              InputProps={{
                startAdornment: (
                  <InputAdornment position="start">
                    <Person color="action" />
                  </InputAdornment>
                ),
              }}
              sx={{ mb: 3 }}
            />

            <TextField
              fullWidth
              label=""
              type={showPassword ? 'text' : 'password'}
              value={password}
              onChange={(e) => setPassword(e.target.value)}
              variant="outlined"
              margin="normal"
              required
              InputProps={{
                startAdornment: (
                  <InputAdornment position="start">
                    <Lock color="action" />
                  </InputAdornment>
                ),
                endAdornment: (
                  <InputAdornment position="end">
                    <IconButton
                      onClick={() => setShowPassword(!showPassword)}
                      edge="end"
                    >
                      {showPassword ? <VisibilityOff /> : <Visibility />}
                    </IconButton>
                  </InputAdornment>
                ),
              }}
              sx={{ mb: 3 }}
            />

            {error && (
              <Alert severity="error" sx={{ mb: 3 }}>
                {error}
              </Alert>
            )}

            <Button
              type="submit"
              fullWidth
              variant="contained"
              size="large"
              disabled={loading}
              sx={{
                py: 1.5,
                fontSize: '1.1rem',
                fontWeight: 600,
                borderRadius: 2,
                background: 'linear-gradient(45deg, #1976d2, #42a5f5)',
                '&:hover': {
                  background: 'linear-gradient(45deg, #1565c0, #1976d2)',
                },
              }}
            >
              {loading ? (
                <CircularProgress size={24} color="inherit" />
              ) : (
                ''
              )}
            </Button>
          </form>

          {/*  */}
          <Box sx={{ mt: 4, textAlign: 'center' }}>
            <Typography variant="body2" color="text.secondary">
              
            </Typography>
            <Typography variant="body2" color="text.secondary" sx={{ mt: 1, fontFamily: 'monospace' }}>
              master / marketlink#2025
            </Typography>
          </Box>

          {/*  */}
          <Card sx={{ mt: 4, backgroundColor: 'info.50', border: '1px solid', borderColor: 'info.200' }}>
            <CardContent sx={{ py: 2 }}>
              <Typography variant="body2" color="info.main" align="center">
                 
              </Typography>
            </CardContent>
          </Card>
        </Paper>
      </Container>
    </Box>
  );
};

export default AdminLogin;
