import React, { useEffect, useState } from 'react';
import {
	AppBar,
	Toolbar,
	Typography,
	Box,
	Container,
	Grid,
	Card,
	CardContent,
	Paper,
	Table,
	TableHead,
	TableRow,
	TableCell,
	TableContainer,
	TableBody,
	Button,
	TextField,
	Dialog,
	DialogTitle,
	DialogContent,
	DialogActions,
	Chip,
	MenuItem,
	Pagination,
} from '@mui/material';
import { LocalShipping, Person } from '@mui/icons-material';
import { useNavigate } from 'react-router-dom';

interface User {
	username: string;
	role: string;
	loginTime: string;
}

interface FinanceTxn {
	id: string;
	type: '' | '';
	category?: string;
	amount: number;
	note: string;
	date: string; // YYYY-MM-DD
}

const AdminFinance: React.FC = () => {
	const navigate = useNavigate();
	const [user, setUser] = useState<User | null>(null);
	const [finance, setFinance] = useState<FinanceTxn[]>([]);
	const [openFinanceDialog, setOpenFinanceDialog] = useState(false);
	const [newTxn, setNewTxn] = useState<FinanceTxn>({ id: '', type: '', category: '', amount: 0, note: '', date: new Date().toISOString().slice(0,10) });
	const [startDate, setStartDate] = useState<string>('');
	const [endDate, setEndDate] = useState<string>('');
	const [typeFilter, setTypeFilter] = useState<'all' | '' | ''>('all');
	const [financePage, setFinancePage] = useState<number>(1);
	const [financePageSize] = useState<number>(8);

	useEffect(() => {
		const raw = localStorage.getItem('adminUser');
		if (!raw) { navigate('/admin/login'); return; }
		try {
			const u = JSON.parse(raw);
			setUser(u);
			if (!['accountant','manager','master'].includes(u.role)) { navigate('/admin/dashboard'); return; }
			const rf = localStorage.getItem('finance');
			setFinance(rf ? JSON.parse(rf) : []);
		} catch { navigate('/admin/login'); }
	}, [navigate]);

	const inRange = (d: string) => {
		if (startDate && d < startDate) return false;
		if (endDate && d > endDate) return false;
		return true;
	};

	const filteredFinance = finance.filter(t => inRange(t.date) && (typeFilter === 'all' || t.type === typeFilter));
	const totalFinancePages = Math.max(1, Math.ceil(filteredFinance.length / financePageSize));
	const financePageData = filteredFinance.slice((financePage - 1) * financePageSize, financePage * financePageSize);

	useEffect(() => { setFinancePage(1); }, [startDate, endDate, typeFilter, finance.length]);

	const financeSummary = filteredFinance.reduce((acc, t) => {
		if (t.type === '') acc.income += t.amount; else acc.expense += t.amount;
		return acc;
	}, { income: 0, expense: 0 });

	const handleAddTxn = () => {
		if (!newTxn.amount || newTxn.amount <= 0) return;
		const entry = { ...newTxn, id: Date.now().toString() };
		const updated = [entry, ...finance];
		setFinance(updated);
		localStorage.setItem('finance', JSON.stringify(updated));
		setOpenFinanceDialog(false);
		setNewTxn({ id: '', type: '', category: '', amount: 0, note: '', date: new Date().toISOString().slice(0,10) });
	};

	if (!user) return null;

	return (
		<Box sx={{ minHeight: '100vh', backgroundColor: 'grey.50' }}>
			<AppBar position="static" sx={{ backgroundColor: 'white', color: 'text.primary' }}>
				<Toolbar>
					<LocalShipping sx={{ color: 'primary.main', mr: 2 }} />
					<Typography variant="h6" component="div" sx={{ color: 'primary.main', fontWeight: 600 }}>
						MARKETLINK EXPRESS 
					</Typography>
					<Box sx={{ flexGrow: 1 }} />
					{/*  */}
					<Box sx={{ display: 'flex', gap: 1, mr: 2 }}>
						<Button size="small" onClick={() => navigate('/admin/dashboard')}></Button>
						<Button size="small" variant="contained" onClick={() => navigate('/admin/finance')}></Button>
						<Button size="small" onClick={() => navigate('/admin/inventory')}></Button>
					</Box>
					{/*  */}
					<Pagination count={totalFinancePages} page={financePage} onChange={(_, p)=>setFinancePage(p)} size="small" color="primary" />
					<Box sx={{ display: 'flex', alignItems: 'center', gap: 2, ml: 2 }}>
						<Typography variant="body2" color="text.secondary">{user.username} ({user.role})</Typography>
						<Person />
					</Box>
				</Toolbar>
			</AppBar>

			<Container maxWidth="lg" sx={{ py: 4 }}>
				<Grid container spacing={3} sx={{ mb: 2 }}>
					<Grid item xs={12} sm={6} md={3}>
						<Card>
							<CardContent sx={{ textAlign: 'center' }}>
								<Typography variant="body2" color="text.secondary"></Typography>
								<Typography variant="h5" sx={{ fontWeight: 700, color: 'success.main' }}>{financeSummary.income.toLocaleString()} </Typography>
							</CardContent>
						</Card>
					</Grid>
					<Grid item xs={12} sm={6} md={3}>
						<Card>
							<CardContent sx={{ textAlign: 'center' }}>
								<Typography variant="body2" color="text.secondary"></Typography>
								<Typography variant="h5" sx={{ fontWeight: 700, color: 'error.main' }}>{financeSummary.expense.toLocaleString()} </Typography>
							</CardContent>
						</Card>
					</Grid>
					<Grid item xs={12} sm={6} md={3}>
						<Card>
							<CardContent sx={{ textAlign: 'center' }}>
								<Typography variant="body2" color="text.secondary"></Typography>
								<Typography variant="h5" sx={{ fontWeight: 700 }}>{(financeSummary.income - financeSummary.expense).toLocaleString()} </Typography>
							</CardContent>
						</Card>
					</Grid>
					<Grid item xs={12} sm={6} md={3}>
						<Button fullWidth variant="contained" onClick={() => setOpenFinanceDialog(true)}></Button>
					</Grid>
				</Grid>

				<Paper sx={{ p: 2, mb: 2 }}>
					<Grid container spacing={2} alignItems="center">
						<Grid item xs={12} md={3}>
							<TextField fullWidth label="" type="date" value={startDate} onChange={(e)=>setStartDate(e.target.value)} InputLabelProps={{ shrink: true }} />
						</Grid>
						<Grid item xs={12} md={3}>
							<TextField fullWidth label="" type="date" value={endDate} onChange={(e)=>setEndDate(e.target.value)} InputLabelProps={{ shrink: true }} />
						</Grid>
						<Grid item xs={12} md={3}>
							<TextField select fullWidth label="" value={typeFilter} onChange={(e)=>setTypeFilter(e.target.value as any)}>
								<MenuItem value="all"></MenuItem>
								<MenuItem value=""></MenuItem>
								<MenuItem value=""></MenuItem>
							</TextField>
						</Grid>
						<Grid item xs={12} md={3} sx={{ display: 'flex', gap: 1 }}>
							<Button variant="outlined" onClick={()=>{ const t=new Date().toISOString().slice(0,10); setStartDate(t); setEndDate(t); }}></Button>
							<Button variant="outlined" onClick={()=>{ const now=new Date(); const t=now.toISOString().slice(0,10); const first=new Date(now.getFullYear(), now.getMonth(), 1).toISOString().slice(0,10); setStartDate(first); setEndDate(t); }}></Button>
							<Button variant="text" onClick={()=>{ setStartDate(''); setEndDate(''); setTypeFilter('all'); }}></Button>
						</Grid>
					</Grid>
				</Paper>

				<Paper>
					<TableContainer>
						<Table>
							<TableHead>
								<TableRow sx={{ backgroundColor: 'grey.50' }}>
									<TableCell></TableCell>
									<TableCell></TableCell>
									<TableCell></TableCell>
									<TableCell></TableCell>
									<TableCell></TableCell>
								</TableRow>
							</TableHead>
							<TableBody>
								{financePageData.map(txn => (
									<TableRow key={txn.id}>
										<TableCell>{txn.date}</TableCell>
										<TableCell>
											<Chip label={txn.type} color={txn.type === '' ? 'success' : 'error'} size="small" />
										</TableCell>
										<TableCell>{txn.category || ''}</TableCell>
										<TableCell>{txn.amount.toLocaleString()}</TableCell>
										<TableCell>{txn.note}</TableCell>
									</TableRow>
								))}
							</TableBody>
						</Table>
					</TableContainer>
				</Paper>

				<Typography variant="subtitle1" sx={{ mt: 3, mb: 1, fontWeight: 600 }}></Typography>
				<Paper>
					<TableContainer>
						<Table>
							<TableHead>
								<TableRow sx={{ backgroundColor: 'grey.50' }}>
									<TableCell></TableCell>
									<TableCell></TableCell>
									<TableCell></TableCell>
									<TableCell></TableCell>
								</TableRow>
							</TableHead>
							<TableBody>
								{Object.entries(filteredFinance.reduce<Record<string, { i:number; e:number }>>((acc, t)=>{ const k=t.date; acc[k] ||= { i:0, e:0 }; if (t.type==='') acc[k].i+=t.amount; else acc[k].e+=t.amount; return acc; }, {})).sort((a,b)=>a[0]<b[0]?1:-1).map(([date, v])=> (
									<TableRow key={date}>
										<TableCell>{date}</TableCell>
										<TableCell sx={{ color: 'success.main' }}>{v.i.toLocaleString()}</TableCell>
										<TableCell sx={{ color: 'error.main' }}>{v.e.toLocaleString()}</TableCell>
										<TableCell>{(v.i - v.e).toLocaleString()}</TableCell>
									</TableRow>
								))}
							</TableBody>
						</Table>
					</TableContainer>
				</Paper>
			</Container>

			<Dialog open={openFinanceDialog} onClose={() => setOpenFinanceDialog(false)} maxWidth="sm" fullWidth>
				<DialogTitle></DialogTitle>
				<DialogContent>
					<Grid container spacing={2} sx={{ mt: 1 }}>
						<Grid item xs={12} sm={6}>
							<TextField select fullWidth label="" value={newTxn.type} onChange={(e) => setNewTxn({ ...newTxn, type: e.target.value as any })}>
								<MenuItem value=""></MenuItem>
								<MenuItem value=""></MenuItem>
							</TextField>
						</Grid>
						<Grid item xs={12} sm={6}>
							<TextField fullWidth label="" type="number" value={newTxn.amount} onChange={(e) => setNewTxn({ ...newTxn, amount: Number(e.target.value) })} />
						</Grid>
						<Grid item xs={12}>
							<TextField select fullWidth label="" value={newTxn.category} onChange={(e)=> setNewTxn({ ...newTxn, category: e.target.value })}>
								{(newTxn.type === '' ? ['','','','',''] : ['','','','','','','']).map(c => (
									<MenuItem key={c} value={c}>{c}</MenuItem>
								))}
							</TextField>
						</Grid>
						<Grid item xs={12} sm={6}>
							<TextField fullWidth label="" type="date" value={newTxn.date} onChange={(e) => setNewTxn({ ...newTxn, date: e.target.value })} InputLabelProps={{ shrink: true }} />
						</Grid>
						<Grid item xs={12}>
							<TextField fullWidth label="" value={newTxn.note} onChange={(e) => setNewTxn({ ...newTxn, note: e.target.value })} />
						</Grid>
					</Grid>
				</DialogContent>
				<DialogActions>
					<Button onClick={() => setOpenFinanceDialog(false)}></Button>
					<Button variant="contained" onClick={handleAddTxn}></Button>
				</DialogActions>
			</Dialog>
		</Box>
	);
};

export default AdminFinance;


