import React, { useState, useEffect } from 'react';
import {
  Box,
  Container,
  Typography,
  Grid,
  Card,
  CardContent,
  Paper,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  Button,
  TextField,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  IconButton,
  Chip,
  Alert,
  Snackbar,
  useTheme,
  AppBar,
  Toolbar,
  Avatar,
  Menu,
  MenuItem,
  Divider,
} from '@mui/material';
import {
  LocalShipping,
  Add,
  Edit,
  Delete,
  Search,
  FilterList,
  Dashboard,
  Inventory,
  People,
  Assessment,
  Logout,
  Person,
  Settings,
} from '@mui/icons-material';
import { useNavigate } from 'react-router-dom';

interface Package {
  id: string;
  trackingNumber: string;
  sender: string;
  receiver: string;
  status: string;
  packageType: string;
  weight: string;
  createdAt: string;
  estimatedDelivery: string;
  // /
  dimensions?: { lengthCm: number; widthCm: number; heightCm: number };
  // 
  fee?: number;
}

interface User {
  username: string;
  role: string;
  loginTime: string;
}

interface FinanceTxn {
  id: string;
  type: '' | '';
  amount: number;
  note: string;
  date: string;
  category?: string;
}

const AdminDashboard: React.FC = () => {
  const [user, setUser] = useState<User | null>(null);
  const [packages, setPackages] = useState<Package[]>([]);
  const [searchTerm, setSearchTerm] = useState('');
  const [filterStatus, setFilterStatus] = useState('all');
  const [openDialog, setOpenDialog] = useState(false);
  //  null 
  const [draftPackage, setDraftPackage] = useState<Package | null>(null);
  const [anchorEl, setAnchorEl] = useState<null | HTMLElement>(null);
  const [allUsers, setAllUsers] = useState<Array<{ username: string; role: string; password?: string }>>([]);
  const [openUserDialog, setOpenUserDialog] = useState(false);
  const [newUser, setNewUser] = useState<{ username: string; password: string; role: string }>({ username: '', password: '', role: 'staff' });
  const [finance, setFinance] = useState<FinanceTxn[]>([]);
  const [openFinanceDialog, setOpenFinanceDialog] = useState(false);
  const [newTxn, setNewTxn] = useState<FinanceTxn>({ id: '', type: '', amount: 0, note: '', date: new Date().toISOString().slice(0,10) });
  const [toast, setToast] = useState<{ open: boolean; text: string; severity: 'success' | 'error' | 'info' | 'warning' }>({ open: false, text: '', severity: 'success' });
  const theme = useTheme();
  const navigate = useNavigate();

  // 
  const mockPackages: Package[] = [
    {
      id: '1',
      trackingNumber: 'ML123456789',
      sender: ' - ',
      receiver: ' - ',
      status: '',
      packageType: '',
      weight: '0.5kg',
      createdAt: '2024-01-14',
      estimatedDelivery: '2024-01-15',
    },
    {
      id: '2',
      trackingNumber: 'ML987654321',
      sender: ' - ',
      receiver: ' - ',
      status: '',
      packageType: '',
      weight: '2.0kg',
      createdAt: '2024-01-14',
      estimatedDelivery: '2024-01-16',
    },
    {
      id: '3',
      trackingNumber: 'ML555666777',
      sender: ' - ',
      receiver: ' - ',
      status: '',
      packageType: '',
      weight: '1.5kg',
      createdAt: '2024-01-13',
      estimatedDelivery: '2024-01-14',
    },
  ];

  useEffect(() => {
    // 
    const userData = localStorage.getItem('adminUser');
    if (!userData) {
      navigate('/admin/login');
      return;
    }

    try {
      const userInfo = JSON.parse(userData);
      setUser(userInfo);
      // 
      try {
        const rawPkgs = localStorage.getItem('packages');
        const parsed = rawPkgs ? JSON.parse(rawPkgs) : null;
        if (Array.isArray(parsed)) {
          setPackages(parsed);
        } else {
          setPackages(mockPackages);
          localStorage.setItem('packages', JSON.stringify(mockPackages));
        }
      } catch {
        setPackages(mockPackages);
      }
      // 
      try {
        const rawUsers = localStorage.getItem('users');
        setAllUsers(rawUsers ? JSON.parse(rawUsers) : []);
      } catch { setAllUsers([]); }
      try {
        const rawFinance = localStorage.getItem('finance');
        setFinance(rawFinance ? JSON.parse(rawFinance) : []);
      } catch { setFinance([]); }
    } catch (error) {
      navigate('/admin/login');
    }
  }, [navigate]);

  // 
  useEffect(() => {
    try {
      localStorage.setItem('packages', JSON.stringify(packages));
    } catch {}
  }, [packages]);

  //  null 
  useEffect(() => {
    if (openDialog && !draftPackage) {
      setDraftPackage(createEmptyPackage());
    }
  }, [openDialog, draftPackage]);

  const handleLogout = () => {
    localStorage.removeItem('adminUser');
    navigate('/admin/login');
  };

  const handleEdit = (pkg: Package) => {
    setDraftPackage({ ...pkg });
    setOpenDialog(true);
  };

  const handleDelete = (id: string) => {
    setPackages(packages.filter(pkg => pkg.id !== id));
    setToast({ open: true, text: '', severity: 'success' });
  };

  const handleSave = () => {
    if (!draftPackage) return;

    const isNew = !draftPackage.id;
    const withId: Package = isNew ? { ...draftPackage, id: Date.now().toString() } : draftPackage;

    if (isNew) {
      setPackages([withId, ...packages]);

      // fee0
      const entry: FinanceTxn = {
        id: `${withId.id}-fee`,
        type: '',
        amount: Number(withId.fee || 0),
        note: ` -  ${withId.trackingNumber}`,
        date: withId.createdAt || new Date().toISOString().slice(0,10),
        category: '',
      };
      const updatedFinance = [entry, ...finance];
      setFinance(updatedFinance);
      localStorage.setItem('finance', JSON.stringify(updatedFinance));
      // 
      setSearchTerm('');
      setFilterStatus('all');
      setToast({ open: true, text: '', severity: 'success' });
    } else {
      setPackages(packages.map(pkg => pkg.id === withId.id ? withId : pkg));
      setToast({ open: true, text: '', severity: 'success' });
    }

    setOpenDialog(false);
    setDraftPackage(null);
  };

  const createEmptyPackage = (): Package => {
    const today = new Date();
    const yyyyMmDd = today.toISOString().slice(0, 10);
    const eta = new Date(today.getTime() + 2 * 24 * 60 * 60 * 1000).toISOString().slice(0, 10);
    return {
      id: '',
      trackingNumber: '',
      sender: '',
      receiver: '',
      status: '',
      packageType: '',
      weight: '',
      createdAt: yyyyMmDd,
      estimatedDelivery: eta,
      dimensions: { lengthCm: 0, widthCm: 0, heightCm: 0 },
      fee: 0,
    };
  };

  const filteredPackages = packages.filter(pkg => {
    const matchesSearch = pkg.trackingNumber.toLowerCase().includes(searchTerm.toLowerCase()) ||
                         pkg.sender.toLowerCase().includes(searchTerm.toLowerCase()) ||
                         pkg.receiver.toLowerCase().includes(searchTerm.toLowerCase());
    const matchesStatus = filterStatus === 'all' || pkg.status === filterStatus;
    return matchesSearch && matchesStatus;
  });

  const getStatusColor = (status: string) => {
    switch (status) {
      case '':
        return 'success';
      case '':
        return 'warning';
      case '':
        return 'info';
      default:
        return 'default';
    }
  };

  const stats = [
    { title: '', value: packages.length, icon: <Inventory />, color: 'primary.main' },
    { title: '', value: packages.filter(p => p.status === '').length, icon: <LocalShipping />, color: 'warning.main' },
    { title: '', value: packages.filter(p => p.status === '').length, icon: <Assessment />, color: 'success.main' },
    { title: '', value: packages.filter(p => p.createdAt === new Date().toISOString().split('T')[0]).length, icon: <Add />, color: 'info.main' },
  ];

  const canViewFinance = user && ['accountant', 'manager', 'master'].includes(user.role);
  const canManageUsers = user && ['manager', 'master'].includes(user.role);

  const handleCreateUser = () => {
    if (!newUser.username.trim() || !newUser.password.trim()) return;
    const exists = allUsers.some(u => u.username === newUser.username);
    if (exists) {
      alert('');
      return;
    }
    const updated = [...allUsers, { username: newUser.username, password: newUser.password, role: newUser.role }];
    setAllUsers(updated);
    localStorage.setItem('users', JSON.stringify(updated));
    setOpenUserDialog(false);
    setNewUser({ username: '', password: '', role: 'staff' });
  };

  const financeSummary = finance.reduce((acc, t) => {
    if (t.type === '') acc.income += t.amount; else acc.expense += t.amount;
    return acc;
  }, { income: 0, expense: 0 });

  const handleAddTxn = () => {
    if (!newTxn.amount || newTxn.amount <= 0) return;
    const entry = { ...newTxn, id: Date.now().toString() };
    const updated = [entry, ...finance];
    setFinance(updated);
    localStorage.setItem('finance', JSON.stringify(updated));
    setOpenFinanceDialog(false);
    setNewTxn({ id: '', type: '', amount: 0, note: '', date: new Date().toISOString().slice(0,10) });
  };

  if (!user) {
    return null;
  }

  return (
    <Box sx={{ minHeight: '100vh', backgroundColor: 'grey.50' }}>
      {/*  */}
      <AppBar position="static" sx={{ backgroundColor: 'white', color: 'text.primary' }}>
        <Toolbar>
          <LocalShipping sx={{ color: 'primary.main', mr: 2 }} />
          <Typography variant="h6" component="div" sx={{ flexGrow: 1, color: 'primary.main', fontWeight: 600 }}>
            MARKETLINK EXPRESS 
          </Typography>
          {/*  */}
          <Box sx={{ display: 'flex', gap: 1, mr: 2 }}>
            <Button size="small" variant="contained" onClick={() => navigate('/admin/dashboard')}></Button>
            <Button size="small" onClick={() => navigate('/admin/finance')}></Button>
            <Button size="small" onClick={() => navigate('/admin/inventory')}></Button>
          </Box>

          <Box sx={{ display: 'flex', alignItems: 'center', gap: 2 }}>
            <Typography variant="body2" color="text.secondary">
              {user.username} ({user.role})
            </Typography>
            <IconButton onClick={(e) => setAnchorEl(e.currentTarget)}>
              <Avatar sx={{ width: 32, height: 32, bgcolor: 'primary.main' }}>
                <Person />
              </Avatar>
            </IconButton>
          </Box>
        </Toolbar>
      </AppBar>

      {/*  */}
      <Menu
        anchorEl={anchorEl}
        open={Boolean(anchorEl)}
        onClose={() => setAnchorEl(null)}
      >
        <MenuItem>
          <Person sx={{ mr: 1 }} />
          
        </MenuItem>
        <MenuItem>
          <Settings sx={{ mr: 1 }} />
          
        </MenuItem>
        <Divider />
        <MenuItem onClick={handleLogout}>
          <Logout sx={{ mr: 1 }} />
          
        </MenuItem>
      </Menu>

      <Container maxWidth="lg" sx={{ py: 4 }}>
        {/*  */}
        <Box sx={{ mb: 4 }}>
          <Alert severity="info"></Alert>
        </Box>

        {/* / */}

        {/*  /admin/finance */}

        {/* / */}
        {canManageUsers && (
          <Box sx={{ mb: 4 }}>
            <Typography variant="h6" sx={{ mb: 2, fontWeight: 600 }}></Typography>
            <Paper sx={{ p: 2, mb: 2 }}>
              <Button variant="contained" onClick={() => setOpenUserDialog(true)}></Button>
            </Paper>
            <Paper>
              <TableContainer>
                <Table>
                  <TableHead>
                    <TableRow sx={{ backgroundColor: 'grey.50' }}>
                      <TableCell></TableCell>
                      <TableCell></TableCell>
                    </TableRow>
                  </TableHead>
                  <TableBody>
                    {allUsers.map((u, i) => (
                      <TableRow key={i}>
                        <TableCell>{u.username}</TableCell>
                        <TableCell>{u.role}</TableCell>
                      </TableRow>
                    ))}
                  </TableBody>
                </Table>
              </TableContainer>
            </Paper>
          </Box>
        )}

        {/*  */}

        {/*  */}

        {/*  */}
        <Dialog open={openUserDialog} onClose={() => setOpenUserDialog(false)} maxWidth="sm" fullWidth>
          <DialogTitle></DialogTitle>
          <DialogContent>
            <Grid container spacing={2} sx={{ mt: 1 }}>
              <Grid item xs={12}>
                <TextField fullWidth label="" value={newUser.username} onChange={(e) => setNewUser({ ...newUser, username: e.target.value })} />
              </Grid>
              <Grid item xs={12}>
                <TextField fullWidth label="" type="password" value={newUser.password} onChange={(e) => setNewUser({ ...newUser, password: e.target.value })} />
              </Grid>
              <Grid item xs={12}>
                <TextField select fullWidth label="" value={newUser.role} onChange={(e) => setNewUser({ ...newUser, role: e.target.value })}>
                  <MenuItem value="staff"></MenuItem>
                  <MenuItem value="accountant"></MenuItem>
                  <MenuItem value="manager"></MenuItem>
                </TextField>
              </Grid>
            </Grid>
          </DialogContent>
          <DialogActions>
            <Button onClick={() => setOpenUserDialog(false)}></Button>
            <Button variant="contained" onClick={handleCreateUser}></Button>
          </DialogActions>
        </Dialog>

        {/*  */}
        <Dialog open={openFinanceDialog} onClose={() => setOpenFinanceDialog(false)} maxWidth="sm" fullWidth>
          <DialogTitle></DialogTitle>
          <DialogContent>
            <Grid container spacing={2} sx={{ mt: 1 }}>
              <Grid item xs={12} sm={6}>
                <TextField select fullWidth label="" value={newTxn.type} onChange={(e) => setNewTxn({ ...newTxn, type: e.target.value as any })}>
                  <MenuItem value=""></MenuItem>
                  <MenuItem value=""></MenuItem>
                </TextField>
              </Grid>
              <Grid item xs={12} sm={6}>
                <TextField fullWidth label="" type="number" value={newTxn.amount} onChange={(e) => setNewTxn({ ...newTxn, amount: Number(e.target.value) })} />
              </Grid>
              <Grid item xs={12} sm={6}>
                <TextField fullWidth label="" type="date" value={newTxn.date} onChange={(e) => setNewTxn({ ...newTxn, date: e.target.value })} InputLabelProps={{ shrink: true }} />
              </Grid>
              <Grid item xs={12}>
                <TextField fullWidth label="" value={newTxn.note} onChange={(e) => setNewTxn({ ...newTxn, note: e.target.value })} />
              </Grid>
            </Grid>
          </DialogContent>
          <DialogActions>
            <Button onClick={() => setOpenFinanceDialog(false)}></Button>
            <Button variant="contained" onClick={handleAddTxn}></Button>
          </DialogActions>
        </Dialog>
      </Container>
      {/*  */}
      <Snackbar
        open={toast.open}
        autoHideDuration={2500}
        onClose={() => setToast(prev => ({ ...prev, open: false }))}
        anchorOrigin={{ vertical: 'bottom', horizontal: 'center' }}
      >
        <Alert onClose={() => setToast(prev => ({ ...prev, open: false }))} severity={toast.severity} sx={{ width: '100%' }}>
          {toast.text}
        </Alert>
      </Snackbar>
    </Box>
  );
};

export default AdminDashboard;
