import React from 'react';
import {
  Box,
  Container,
  Typography,
  Grid,
  Card,
  CardContent,
  CardActions,
  Button,
  useTheme,
  useMediaQuery,
} from '@mui/material';
import {
  LocalShipping,
  Flight,
  DirectionsCar,
  Speed,
  Security,
  Support,
  AccessTime,
} from '@mui/icons-material';

const ServicesPage: React.FC = () => {
  const theme = useTheme();
  const isMobile = useMediaQuery(theme.breakpoints.down('md'));

  const services = [
    {
      icon: <LocalShipping sx={{ fontSize: 48, color: 'primary.main' }} />,
      title: '',
      description: '24-48',
      features: ['', '', '', ''],
      price: ' 2,000 ',
      deliveryTime: '24-48',
    },
    {
      icon: <Flight sx={{ fontSize: 48, color: 'primary.main' }} />,
      title: '',
      description: '',
      features: ['', '', '', ''],
      price: ' 15,000 ',
      deliveryTime: '3-7',
    },
    {
      icon: <DirectionsCar sx={{ fontSize: 48, color: 'primary.main' }} />,
      title: '',
      description: '2-4',
      features: ['', '', '', ''],
      price: ' 1,500 ',
      deliveryTime: '2-4',
    },
  ];

  const features = [
    {
      icon: <Speed sx={{ fontSize: 40, color: 'primary.main' }} />,
      title: '',
      description: '',
    },
    {
      icon: <Security sx={{ fontSize: 40, color: 'primary.main' }} />,
      title: '',
      description: '',
    },
    {
      icon: <Support sx={{ fontSize: 40, color: 'primary.main' }} />,
      title: '',
      description: '24/7',
    },
    {
      icon: <AccessTime sx={{ fontSize: 40, color: 'primary.main' }} />,
      title: '',
      description: '',
    },
  ];

  const processSteps = [
    {
      step: '1',
      title: '',
      description: '',
    },
    {
      step: '2',
      title: '',
      description: '',
    },
    {
      step: '3',
      title: '',
      description: '',
    },
    {
      step: '4',
      title: '',
      description: '',
    },
    {
      step: '5',
      title: '',
      description: '',
    },
  ];

  return (
    <Box>
      {/* Hero Section */}
      <Box
        sx={{
          background: 'linear-gradient(135deg, #1976d2 0%, #42a5f5 100%)',
          color: 'white',
          py: { xs: 6, md: 8 },
          textAlign: 'center',
        }}
      >
        <Container maxWidth="lg">
          <Typography
            variant={isMobile ? 'h3' : 'h2'}
            component="h1"
            sx={{ fontWeight: 700, mb: 3 }}
          >
            
          </Typography>
          <Typography variant="h6" sx={{ opacity: 0.9, maxWidth: 600, mx: 'auto' }}>
            
          </Typography>
        </Container>
      </Box>

      <Container maxWidth="lg" sx={{ py: 6 }}>
        {/*  */}
        <Typography
          variant="h3"
          component="h2"
          align="center"
          sx={{ mb: 6, fontWeight: 600 }}
        >
          
        </Typography>

        <Grid container spacing={4} justifyContent="center">
          {services.map((service, index) => (
            <Grid item xs={12} sm={6} md={4} key={index}>
              <Card
                sx={{
                  height: '100%',
                  display: 'flex',
                  flexDirection: 'column',
                  transition: 'all 0.3s ease',
                  '&:hover': {
                    transform: 'translateY(-8px)',
                    boxShadow: '0 8px 30px rgba(0,0,0,0.15)',
                  },
                }}
              >
                <CardContent sx={{ flexGrow: 1, textAlign: 'center' }}>
                  <Box sx={{ mb: 3 }}>{service.icon}</Box>
                  <Typography variant="h5" component="h3" sx={{ mb: 2, fontWeight: 600 }}>
                    {service.title}
                  </Typography>
                  <Typography variant="body2" color="text.secondary" sx={{ mb: 3 }}>
                    {service.description}
                  </Typography>
                  
                  <Box sx={{ mb: 3 }}>
                    {service.features.map((feature, idx) => (
                      <Typography
                        key={idx}
                        variant="body2"
                        sx={{
                          mb: 1,
                          color: 'primary.main',
                          fontWeight: 500,
                        }}
                      >
                         {feature}
                      </Typography>
                    ))}
                  </Box>

                  <Box sx={{ mb: 2 }}>
                    <Typography variant="h6" color="primary.main" sx={{ fontWeight: 600 }}>
                      {service.price}
                    </Typography>
                    <Typography variant="body2" color="text.secondary">
                      {service.deliveryTime}
                    </Typography>
                  </Box>
                </CardContent>
                <CardActions sx={{ justifyContent: 'center', pb: 2 }}>
                  <Button variant="outlined" color="primary">
                    
                  </Button>
                </CardActions>
              </Card>
            </Grid>
          ))}
        </Grid>

        {/*  */}
        <Box sx={{ mt: 8 }}>
          <Typography
            variant="h3"
            component="h2"
            align="center"
            sx={{ mb: 6, fontWeight: 600 }}
          >
            
          </Typography>
          
          <Grid container spacing={4}>
            {features.map((feature, index) => (
              <Grid item xs={12} sm={6} md={3} key={index}>
                <Box sx={{ textAlign: 'center' }}>
                  <Box sx={{ mb: 3 }}>{feature.icon}</Box>
                  <Typography variant="h6" component="h3" sx={{ mb: 2, fontWeight: 600 }}>
                    {feature.title}
                  </Typography>
                  <Typography variant="body2" color="text.secondary">
                    {feature.description}
                  </Typography>
                </Box>
              </Grid>
            ))}
          </Grid>
        </Box>

        {/*  */}
        <Box sx={{ mt: 8 }}>
          <Typography
            variant="h3"
            component="h2"
            align="center"
            sx={{ mb: 6, fontWeight: 600 }}
          >
            
          </Typography>
          
          <Grid container spacing={4}>
            {processSteps.map((step, index) => (
              <Grid item xs={12} sm={6} md={2.4} key={index}>
                <Box sx={{ textAlign: 'center' }}>
                  <Box
                    sx={{
                      width: 60,
                      height: 60,
                      borderRadius: '50%',
                      backgroundColor: 'primary.main',
                      color: 'white',
                      display: 'flex',
                      alignItems: 'center',
                      justifyContent: 'center',
                      fontSize: '1.5rem',
                      fontWeight: 'bold',
                      mx: 'auto',
                      mb: 2,
                    }}
                  >
                    {step.step}
                  </Box>
                  <Typography variant="h6" component="h3" sx={{ mb: 1, fontWeight: 600 }}>
                    {step.title}
                  </Typography>
                  <Typography variant="body2" color="text.secondary">
                    {step.description}
                  </Typography>
                </Box>
              </Grid>
            ))}
          </Grid>
        </Box>

        {/*  */}
        <Box
          sx={{
            mt: 8,
            p: 6,
            backgroundColor: 'primary.main',
            color: 'white',
            borderRadius: 3,
            textAlign: 'center',
          }}
        >
          <Typography variant="h4" component="h3" sx={{ mb: 3, fontWeight: 600 }}>
            
          </Typography>
          <Typography variant="h6" sx={{ mb: 4, opacity: 0.9 }}>
            
          </Typography>
          <Button
            variant="outlined"
            size="large"
            href="/contact"
            sx={{
              color: 'white',
              borderColor: 'white',
              px: 4,
              py: 1.5,
              fontSize: '1.1rem',
              fontWeight: 600,
              '&:hover': {
                borderColor: 'white',
                backgroundColor: 'rgba(255,255,255,0.1)',
              },
            }}
          >
            
          </Button>
        </Box>
      </Container>
    </Box>
  );
};

export default ServicesPage;
